.ellipse {
  width: 29px;
  height: 29px;
  border: 6px solid #FF1D6C;
  border-radius: 100%;
  top: 36px;
  left: 128px; }

.square {
  width: 28px;
  height: 28px;
  border: 6px solid #FF1D6C;
  top: 36px;
  left: 277px; }

.i-icon {
  width: 8px;
  height: 32px;
  background-color: #FF1D6C;
  position: relative;
  top: 33px;
  left: 370px; }
  .i-icon .i-triangle {
    width: 0;
    height: 0;
    border-style: solid;
    border-color: transparent transparent #FF1D6C transparent;
    border-width: 0px 12px 20px 12px;
    position: absolute;
    top: -27px;
    right: -9px;
    transform: rotate(20deg); }

.menu-primary {
  width: 34px;
  height: 34px;
  border: 15px solid #FF1D6C;
  border-radius: 100%;
  background-color: #FF1D6C;
  position: relative; }
  .menu-primary:hover {
    transform: scale(1.1); }
  .menu-primary .menu-primary-inner {
    width: 0;
    height: 0;
    border-style: solid;
    border-color: #FF1D6C #FF1D6C #FFFFFF #FF1D6C;
    border-width: 0 5px 10px 5px;
    position: absolute;
    top: -3px;
    right: -3px; }

.menu-secondary {
  width: 34px;
  height: 34px;
  border: 15px solid #FFB72C;
  border-radius: 100%;
  background-color: #FFB72C;
  position: relative; }
  .menu-secondary:hover {
    transform: scale(1.1); }
  .menu-secondary .menu-secondary-inner {
    width: 8px;
    height: 8px;
    border-style: solid;
    border-color: #FFFFFF;
    background-color: #FFFFFF;
    position: absolute;
    top: -2px;
    right: -2px; }

.menu-third {
  width: 34px;
  height: 34px;
  border: 15px solid #007350;
  border-radius: 100%;
  background-color: #007350;
  position: relative; }
  .menu-third:hover {
    transform: scale(1.1); }
  .menu-third .menu-third-inner {
    width: 10px;
    height: 10px;
    background-color: #FFFFFF;
    border-color: #FFFFFF;
    border-style: solid;
    border-radius: 100%;
    position: absolute;
    top: -3px;
    right: -3px; }

.admit {
  cursor: pointer;
  background: #FF1D6C;
  color: #FFFFFF;
  font-family: "Noto Sans TC", sans-serif;
  font-size: 14px;
  width: 40px;
  height: 40px;
  display: block;
  line-height: 40px;
  text-align: center;
  border-radius: 6px;
  box-shadow: 0px 2px 4px 0px #0D0B0C33; }

.tag-primary {
  width: 0;
  height: 0;
  border-style: solid;
  border-color: transparent transparent #FF1D6C transparent;
  border-width: 0px 12px 20px 12px; }

.tag-secondary {
  width: 20px;
  height: 20px;
  background: #FFB72C; }

.scroll {
  width: 32px;
  height: 32px;
  background: #0D0B0C;
  box-shadow: 0px 4px 3px 0px #0D0B0C33;
  border-radius: 6px;
  cursor: pointer;
  top: 100px;
  left: 21px; }
  .scroll .right {
    width: 0;
    height: 0;
    border-style: solid;
    border-color: transparent transparent transparent white;
    border-width: 5px 0px 5px 7px;
    top: 10px;
    left: 13px; }

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle; }

